.App {
  display: flex;
  flex-direction: column;
  flex: 1 1;
  margin-left: 10px;
  font-family: "Roboto";
}

.title {
  padding-top: 20px;
  font-weight: 700;
  text-align: center;
}

.titleH5 {
  padding-top: 10px;
  font-weight: 700;
  text-align: center;
}

.titleH5WithoutPadding {
  font-weight: 700;
  text-align: center;
}

.loader {
  margin-top: 50px;
  text-align: center;
}

.footercontainer {
  display: flex;
  align-items: center;
  flex-direction: column;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}

.tweetbutton {
  margin-top: 10px;
  padding: 5px 10px;
  background-color: #1d9bf0;
  color: white;
  text-align: center;
  border-radius: 5px;
}

.tweetlabel {
  margin-left: 5px;
  white-space: nowrap;
}

ul {
  list-style: none;
  margin-left: 0;
  padding-left: 0;
}

li.rule-item-cross {
  padding-left: 1em;
  text-indent: -1em;
}

li.rule-item-cross:before {
  content: "\292B";
  color: red;
  font-size: 18px;
  padding-bottom: 5px;
  padding-right: 5px;
}

li.rule-item-check:before {
  content: "\2713";
  color: green;
  font-size: 16px;
  padding-right: 5px;
}

@media (min-width: 1281px) {
  .container {
    width: 60%;
    margin: 0 auto;
  }
}

@media (min-width: 1025px) and (max-width: 1280px) {
  .container {
    width: 70%;
    margin: 0 auto;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .container {
    width: 75%;
    margin: 0 auto;
  }
}

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .container {
    width: 80%;
    margin: 0 auto;
  }
}

@media (min-width: 481px) and (max-width: 767px) {
  .container {
    width: 85%;
    margin: 0 auto;
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  .container {
    width: 90%;
    margin: 0 auto;
  }
}

